import headnav from '../../components/headnav/headnav.vue';
export default {
	components: {
		headnav
	},
	data() {
		return {
			active: 1
		};
	},
	onPageScroll(Object) {
		if (Object.scrollTop >30 ) {
			this.$refs.headnav.changeColor(false);
		} else {
			this.$refs.headnav.changeColor(true);
		}
	},
	methods: {
		changeActive: function(e) {
			if (e == 1) {
				this.active = 1;
				uni.navigateTo({
					url:'selectPrice'
				})
			} else {
				this.active = 2;
			}
		},
		goRoom: function() {
			uni.navigateTo({
				url: 'roomDetails'
			});
		}
	}
};
